import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs';
import { isBuffer } from 'util';
import IUser from '../models/user.model';
import { HomeService } from '../service/home.service';
import { ProfileService } from '../service/profile.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

    user:String

  salonServices = [
    { name: 'Haircut',icon:"./assets/icons/scissor.svg" },
    { name: 'Shampoo',icon:"./assets/icons/shampoo.svg" },
    { name: 'Shaving',icon:"./assets/icons/shave.svg" },
    { name: 'Hair\xa0Color',icon:"./assets/icons/hair_color.svg"}
  ];

  nearbyShops:any = []

  constructor(private homeService:HomeService,private profileService:ProfileService) { }
  ngOnInit(): void {

    this.profileService.getUser().subscribe((user:any)=>{
      console.log(user)
      this.user = user.name
    })
    
    // this.profileService.getUser().subscribe(
    //   (user)=>{
    //     this.user = user.user
    //   }
    // )

    this.homeService.nearbyBarbershop().subscribe((shop)=>{
      this.nearbyShops = shop
      console.log(shop)
    })


  }

}
